import styled from "styled-components";

export const TaskContainer = styled.div`
  width: 100%;
  padding: 12px;
  margin-top: 18px;
  display: flex;
  flex-direction: column;

  * {
    color: #F5EAEA;
  }
`

export const TaskItem = styled.h3`
  font-style: normal;
  font-weight: 700;
  font-size: 24px;
  line-height: 25px;
  margin-top: 2px;
`

export const TaskDescription = styled.p`
  margin: 18px 0;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 22px;
  line-break: anywhere;
`